a, p, h1, h2, h3, h4, h5, h6, li, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Gilroy", sans-serif !important;
}

.btn_naranja {
  background-color: #FF4419;
  color: white;
  font-family: "Gilroy", sans-serif;
  font-size: 14px;
}
.btn_naranja:hover {
  background-color: #FF4419;
  color: white;
}

.menu-top {
  border-bottom: 1px solid #FF4419;
}
.menu-top .logo-yaguar {
  max-height: 51px;
  width: auto;
}
@media (max-width: 991px) {
  .menu-top .logo-yaguar {
    max-height: 40px;
    padding: 5px;
  }
}
.menu-top .nav-link {
  font-family: "Gilroy", sans-serif;
  font-size: 14px;
  font-weight: medium;
}
.menu-top-collapse.navbar-collapse {
  flex-grow: unset;
}
@media (max-width: 991px) {
  .menu-top .collapse-hidden_pc {
    display: block !important;
  }
  .menu-top .collapse-hidden_movil {
    display: none !important;
  }
}
@media (min-width: 992px) {
  .menu-top .collapse-hidden_pc {
    display: none !important;
  }
  .menu-top .collapse-hidden_movil {
    display: flex !important;
  }
}

header .py-header {
  padding: 100px 0;
}
header .header-titulo {
  font-size: 60px;
  font-weight: bold;
  line-height: 68px;
  font-family: "Gilroy", sans-serif !important;
  text-align: left;
}
header .header-descripcion {
  font-size: 20px;
  font-family: "Gilroy", sans-serif;
}
header .header-btn {
  width: 198px;
  font-family: "Gilroy", sans-serif;
  font-size: 16px;
}
header .header-imagen {
  width: 100%;
}
header .header-imagen-hidden_pc {
  display: none;
}
header .header-imagen-hidden_movil {
  display: block;
}
@media (max-width: 991px) {
  header .header-titulo {
    font-size: 38px;
    font-weight: bold;
    line-height: 48px;
    font-family: "Gilroy", sans-serif !important;
    text-align: center;
  }
  header .header-descripcion {
    font-size: 16px;
    font-family: "Gilroy", sans-serif;
    text-align: center;
  }
  header .header-btn {
    width: 100%;
    font-family: "Gilroy", sans-serif;
    font-size: 16px;
  }
  header .header-imagen {
    width: 98%;
  }
  header .header-imagen-hidden_movil {
    display: none;
  }
  header .header-imagen-hidden_pc {
    display: block;
    padding: 25px 0;
  }
}

footer {
  width: 100%;
  border-top: 1px solid #FF4419;
  padding-bottom: 20px;
}
footer .footer li {
  list-style: none;
}